<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">导航栏管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
        <!--<el-button
          type="text"
          style="float: right; padding: 3px 0"
          @click="moreSelect"
        ><i :class="selectClass"></i>更多选项</el-button>-->
        <div
          class="advanced-box"
          v-if="content_show"
        >
          <el-form
            :inline="true"
            :model="forms"
            class="demo-form-inline"
          >
            <el-form-item label="id">
              <el-input v-model="formNews.parent_id"></el-input>
            </el-form-item>
            <el-form-item label="显示文字">
              <el-input v-model="formNews.title"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="onSubmit"
              >查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="reset">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="60"
      >
      </el-table-column>
      <el-table-column
        prop="parent_id"
        label="父节点id"
        align="center"
        width="90"
      >
      </el-table-column>
      <el-table-column
        prop="title"
        label="显示文字"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="status_name"
        label="状态"
        align="center"
        width="110"
      >
      </el-table-column>
      <el-table-column
        prop="release_time"
        label="发布时间"
        align="center"
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="update_user"
        label="更新人"
        align="center"
        width="170"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="250"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
          <el-button
            size="mini"
            @click="handleStatus(scope.$index, scope.row)"
            v-if="scope.row.status"
          >取消启用</el-button>
          <el-button
            size="mini"
            @click="handleStatus(scope.$index, scope.row)"
            v-else
          >启用</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[15, 20, 30]"
        :page-size="page_size"
        layout="sizes, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="id"
            prop="id"
          >
            <el-input v-model="formLabelAlign.id"></el-input>
          </el-form-item>
          <el-form-item
            label="父节点id"
            prop="parent_id"
          >
            <el-input v-model="formLabelAlign.parent_id"></el-input>
          </el-form-item>
          <el-form-item
            label="显示文字"
            prop="title"
          >
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item label="源链接">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
          <el-form-item label="是否启用" prop="status">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">未启用</el-radio>
              <el-radio :label="1">已启用</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="发布时间"
            prop="release_time"
          >
            <el-date-picker
              v-model="formLabelAlign.release_time"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择日期"
              default-time="01:01:01"
            >
            </el-date-picker>
          </el-form-item>
          <el-row :gutter="10">
            <el-col :span="10">
              <el-form-item
                label="创建者"
                prop="create_user"
              >
                <el-input
                  v-model="formLabelAlign.create_user"
                  :disabled="true"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item label="创建时间">
                <el-input
                  v-model="formLabelAlign.create_time"
                  :disabled="true"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addNewsInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
export default {
  data() {
    return {
      auth_name: "",
      selectClass: "el-icon-arrow-down",
      content_show: false,
      forms: {
        parent_id: 0,
        title:''
      },
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        id:0,
        parent_id:0,
        title: "",
        status: 0,
        release_time: "",
        create_user: "",
        create_time: "",
        link: ""
      },
      rules: {
        parent_id: [{ required: true, message: "请输入活动名称", trigger: "blur" }],
        status: [{ required: true}],
        create_user: [{ required: true }],
        create_time: [{ required: true }],
        release_time: [{ required: true }]
      },
      token:'',
    };
  },
  mounted() {
    this.auth_name = storageHelper.getItem(authName, "healthcare");
    this.token = storageHelper.getItem(authToken, "healthcare");
    this.getSolutionList("1", "15");
  },
  methods: {
    handleSizeChange(val) {
      this.getSolutionList(this.currentPage, val);
    },
    handleCurrentChange(val) {
      this.getSolutionList(val, this.page_size);
    },
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addNewsInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    openAddDialog() {
      this.formLabelAlign = {
        id:0,
        parent_id:0,
        title: "",
        status: 0,
        create_user: this.auth_name,
        create_time: getCurTime(),
        link: ""
      };
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      var that = this;
      var fd = new window.FormData();
      fd.append("file", file);
      axios
        .post("core/file/upload", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: this.token
          }
        })
        .then(function(res) {
          if (res.code == 0) {
            var url = res.data.url;
            that.formLabelAlign.ico_name = file.name;
            that.formLabelAlign.icon = res.data.code;
            that.$message({
              type: "success",
              message: "上传成功！"
            });
          }
          //console.log(res);
        });
      return false; // 返回false不会自动上传
    },
    moreSelect() {
      if (this.selectClass == "el-icon-arrow-down") {
        this.selectClass = "el-icon-arrow-up";
        this.content_show = true;
      } else {
        this.selectClass = "el-icon-arrow-down";
        this.content_show = false;
      }
    },
    //查询
    onSubmit() {
      this.getSolutionList("1", "15");
    },
    //重置
    reset() {
      this.forms = {
        parent_id: 0,
        title:''
      };
      this.getSolutionList("1", "15");
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        nav_id:row.nav_id,
        id:row.id,
        parent_id:row.parent_id,
        title: row.title,
        status: row.status,
        release_time: row.release_time,
        create_user: row.create_user,
        create_time: row.create_time,
        link: row.link
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          var params = { nav_id: row.nav_id };
          that.submitSolutionInfo("content/nav/delete", params, "删除成功!");
        })
        .catch(() => {});
    },
    handleStatus(index, row) {
      var params = { nav_id: row.nav_id };
      this.submitSolutionInfo("content/nav/updateStatus", params, "操作成功!");
    },
    //编辑框提交按钮
    addNewsInfo(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("nav_id")) {
            method = "content/nav/update";
            info = "修改成功!";
          } else {
            method = "content/nav/create";
            info = "添加成功!";
          }
          that.submitSolutionInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    getEditorContent(content) {
      this.formLabelAlign.content = content;
    },
    getSolutionList(page_index, page_size) {
      var params = {
        parent_id:this.forms.parent_id,
        title:this.forms.title,
        page_index: page_index,
        page_size: page_size
      };
      var that = this;
      axios.get("content/nav/pageList", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var rdata = response.data;
            var count = rdata.count;
            var page_index = rdata.page_index;
            var page_size = rdata.page_size;
            that.currentPage = page_index;
            that.page_size = page_size;
            that.total = count;
            var des_num = (page_index - 1) * page_size;
            var json = rdata.rows;
            for (var i = 0; i < json.length; i++) {
              var status = json[i].status;
              json[i].create_time = utcTimeChange(json[i].create_time);
              json[i].release_time = utcTimeChange(json[i].release_time);
              json[i].update_time = utcTimeChange(json[i].update_time);
              var status_name = "启用";
              if (status == 0) {
                status_name = "未启用";
              }
              json[i].index = des_num + i + 1;
              json[i].status_name = status_name;
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitSolutionInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getSolutionList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    },
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


